.filters{width:232px;height:252px;flex-shrink:0;background-color:#fff;box-sizing:border-box;border-radius:5px;box-shadow:0px 2px 8px rgba(0,0,0,.1)}.header{color:#4a4a4a;font-family:Open Sans;font-size:12px;font-style:normal;font-weight:600;line-height:12px;letter-spacing:.5px;padding:20px 20px 10px 20px}.filters input[type=checkbox]{display:none}.filters label:before{content:"";display:inline-block;width:20px;height:20px;border:1px solid #2196f3;border-radius:2px;margin-right:10px;vertical-align:middle}.filters input[type=checkbox]:checked+label:before{content:"✓";color:#2196f3;text-align:center;line-height:20px;font-size:14px;font-weight:bold}.filters label{display:flex;align-items:center;color:#4a4a4a;font-family:Open Sans;font-size:13px;font-style:normal;font-weight:400;line-height:20px;cursor:pointer;padding:0 20px;height:40px}.filters label:hover{background-color:#f1fcff}input{cursor:pointer}